import React from 'react';
import { Link } from 'react-router-dom';
import "../component/navigation.css";
const RouterPage = () => {
    return (
        <div className="navigation">
            <Link class="navbar-brand nav-link" style={{ color: "#333" }} to="/Income" >Income</Link>
            <Link class="navbar-brand nav-link" style={{ color: "#333" }} to="">|</Link>
            <Link class="navbar-brand nav-link" style={{ color: "#333" }} to="/">Expense</Link>
        </div>

    )
}
export default RouterPage;


