import React, { Component } from 'react'
import "../component/display.css";
import { connect } from "react-redux";
class display extends Component {
   remove = (e) =>
   {
    let id =  e.target.id;
    this.props.delete(id);
   }
   editExpense = (e) =>
   {
    let id =  e.target.id;
    console.log(id);
    this.props.edit(id);
   }
    render() {
        return (
            <div className="displaydetails">
                <h1 className="title3">Expenses</h1>
                <div className="row">
                    <div className="sno">Sno</div>
                    <div className="expensetitle">Expense</div>
                    <div className="costtitle">Cost</div>
                    <div className="del"></div>
                    <div className="del"></div>
                </div>
                {
                    this.props.data.map((data, index) =>

                        <li className="row" key={index}>
                            <div className="sno">{index + 1}</div>
                            <div className="expenserecord">{data.expense}</div>
                            <div className="costrecord">{data.cost}</div>
                            <i id={index} onClick={this.remove} class="del fas fa-trash"></i>
                            <i id={index} class="del fas fa-edit" onClick ={this.editExpense}></i>
                        </li>
                    )

                }
            </div>
        )
    }
}
const mapPropsToState = (state) =>
{
    return{
        data : state.ExpensesRecord
    }

}
const mapStateToProps = (dispatch) =>
{
    return{
        delete : (id)=> dispatch({type:"DELETE", id:id}),
        edit : (id)=> dispatch({type:"EDIT", id:id}),
    }
}
export default connect(mapPropsToState,mapStateToProps)(display);



