const initialstate = {
    Expenses : {
        expense : "",
        cost : 0
    },
    ExpensesRecord : [],
    EditState : 0,
    EditId : 0,
    Income : 0,
    IncomeValue : 0
}

const Reducer = (state = initialstate , action) =>
{
    if(action.type === "EXPENSE-FIELD")
    {
        const expense = action.expenseChange;
        const cost = state.Expenses.cost;
        const newExpenses = {expense , cost};
        return{
        ...state,
        Expenses : newExpenses
        }

    }
    if(action.type === "COST-FIELD")
    {
        const expense = state.Expenses.expense;
        const cost = action.costChange;
        const newExpenses = {expense , cost};
        console.log(newExpenses)
        return{
        ...state,
        Expenses : newExpenses
        }

    }
    if(action.type === "ADD-EXPENSE")
    {
        const expensesRecord = state.Expenses;
        let stateExpense  = [...state.ExpensesRecord];
        if(state.EditState === 0)
        {
        stateExpense.push(expensesRecord);
        }
        else{
            let id = state.EditId;
            stateExpense[id].expense = expensesRecord.expense;
            stateExpense[id].cost = expensesRecord.cost;
        }
        let newExpenses = {...state.Expenses};
        newExpenses.expense = "";
        newExpenses.cost = "";
        console.log(stateExpense);
        return{
            ...state,
            ExpensesRecord : stateExpense,
            Expenses : newExpenses,
            EditId : 0,
            EditState : 0
        }
    }
    if(action.type === "DELETE")
    {
        let stateExpense  = [...state.ExpensesRecord];
        let id = action.id;
        stateExpense.splice(id , 1);
        return{
            ...state,
            ExpensesRecord : stateExpense,
        }

    }
    if(action.type === "EDIT")
    {
        let stateExpense  = [...state.ExpensesRecord];
        let id = action.id;
        let newExpenses = {...state.Expenses};
        newExpenses.expense = stateExpense[id].expense;
        newExpenses.cost = stateExpense[id].cost;
        return{
            ...state,
            Expenses : newExpenses,
            EditState : 1,
            EditId : id
        }

    }
    if(action.type === "INCOME-FIELD")
    {
        const income = action.incomeChange;
        console.log(income);
        return{
        ...state,
        IncomeValue : income
        }

    }
    if(action.type === "ADD-INCOME")
    {
        const incomeRecord = state.IncomeValue;  
        return{
            ...state,
           IncomeValue : "",
           Income : incomeRecord
        }
    }
    return state;
}
export default Reducer; 


