import React, { Component } from 'react'
import "../component/income.css";
import {connect} from 'react-redux';
class Income extends Component {
    Income = (e) =>
    {
       this.props.incomeHandler(e.target.value);
    }
    
    adddetails = (e) =>
    {
        e.preventDefault();
        document.getElementById("budgetform").reset();
        this.props.addHandler();
    }
    render() {
        return (
            <div className="Incomeform">
                <h2 className="title2">your Income.</h2>
                <form id="budgetform" onSubmit={this.adddetails}>
                    <div className="row">
                        <input type="text" id="i1" onChange={this.Income}
                        value={this.props.income} placeholder="Your Income"required />
                        <button type="submit">Add</button>
                    </div>
                </form>
            </div>
        )
    }
}
const matchPropsToState = (state) =>
{
  return{
    income : state.IncomeValue
  }
}
const matchStateToProps = dispatch =>
{

    return{
        incomeHandler : (income) => dispatch({ type:"INCOME-FIELD" ,incomeChange : income}),
        
        addHandler: () => dispatch({type:"ADD-INCOME"})
    }
}
export default connect(matchPropsToState,matchStateToProps)(Income);


