import React, { Component } from 'react';
import { connect } from 'react-redux';
import "../component/amount.css";
class amount extends Component {
    render() {
        var amount = 0;
        return (
            <div className="amount">
                <div class="alert alert-light">
                {
                    this.props.details.map((expense) =>
                    {
                        amount += parseFloat(expense.cost);
                        console.log(amount);
                    })
                    
                }
                    Income :  <span className="space primary-text">{this.props.income} Rs.</span>
                    
                    Amount left :  <span className="space secondary-text">{this.props.income - amount} Rs.</span>

                </div>
            </div>
        )
    }
}
let mapStateToProps = (state) =>
{
    return{
        details : state.ExpensesRecord,
        income : state.Income
   }   
}
export default connect(mapStateToProps)(amount);


