import React, { Component } from 'react'
import "../component/expense.css";
import {connect} from 'react-redux';
class Expense extends Component {
    Expense = (e) =>
    {
       this.props.expenseHandler(e.target.value);
    }
    Cost = (e) =>
    {
        this.props.costHandler(e.target.value);
    }
    adddetails = (e) =>
    {
        e.preventDefault();
        document.getElementById("budgetform").reset();
        if(this.props.income === 0)
        {
            alert("Enter some Income");
        }
        else{
            this.props.addHandler();
        }
    }
    render() {
        return (
            <div className="Expenseform">
                <h2 className="title2">Add your Expenses.</h2>
                <form id="budgetform" onSubmit={this.adddetails}>
                    <div className="row">
                        <input type="text" id="i1" onChange={this.Expense}
                        value={this.props.expense.expense} placeholder="Your Expenses"  required/>
                        <input type="number" id="i2" onChange={this.Cost}
                        value={this.props.expense.cost} placeholder="amount" required/>
                        <button type="submit">Add</button>
                    </div>
                </form>
            </div>
        )
    }
}
const matchPropsToState = (state) =>
{
  return{
    expense : state.Expenses,
    income : state.Income
  }
}
const matchStateToProps = dispatch =>
{

    return{
        expenseHandler : (expense) => dispatch({ type:"EXPENSE-FIELD" ,expenseChange :expense}),

        costHandler : (cost) => dispatch({ type:"COST-FIELD" ,costChange :cost}),

        addHandler: () => dispatch({type:"ADD-EXPENSE"})
    }
}
export default connect(matchPropsToState,matchStateToProps)(Expense);


