import React, { Component } from 'react';
import Form from "../src/component/form";
import Display from "../src/component/display";
import Amount from "../src/component/amount";
import "../src/App.css";


 class App extends Component {
  constructor(props)
  {
    super(props);
    this.state = {
      expenses :[]
    }
  }
  addExpenses = (expense) =>
  {
    let expenses = this.state.expenses;
    expenses.push(expense);
    this.setState({
      expenses : expenses
    })
    console.log(expenses);
  }

  deleteExpense = (id) =>
  {
    let expenses = this.state.expenses;
    expenses.splice(id , 1);
    this.setState({
      expenses : expenses
    })
    console.log(expenses);  
  }
  render() {
    return (
      <div>
       <h1 className="title"> Budget Manager{this.props.ctr}</h1>
       <Display del = {(id)=>{this.deleteExpense(id)}} />
       <Amount/>
       <Form />
      </div>
    )
  }
}

export default App;


